
// Imports
import { ref } from "vue";
import {
  IonContent,
  IonHeader,
  IonPage,
  IonTitle,
  IonToolbar,
  IonRow,
  IonGrid,
  IonCol,
  IonItem,
  IonInput,
  IonButton,
  IonImg,
  toastController,
} from "@ionic/vue";
import { defineComponent } from "vue";
import { useRouter } from "vue-router";
import { useStore } from "vuex";
import { useI18n } from "vue-i18n";

export default defineComponent({
  name: "Login",
  components: {
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar,
    IonRow,
    IonGrid,
    IonCol,
    IonItem,
    IonInput,
    IonButton,
    IonImg,
  },
  setup() {
    // Define router
    const router = useRouter();

    // Define store
    const store = useStore();

    // define i18n
    const i18n = useI18n();

    // Define form values
    const email = ref("");
    const password = ref("");

    // Define reset password state
    const showResetPassword = ref(false);

    // Define reset password method
    const resetPassword = async function () {
      if (email.value !== "") {
        try {
          await store.dispatch("resetPassword", email.value);
          const toast = await toastController.create({
            message: i18n.t("resetEmailSend"),
            duration: 3000,
            color: "success",
          });
          toast.present();
          showResetPassword.value = false;
        } catch (error) {
          const toast = await toastController.create({
            message: error,
            duration: 3000,
            color: "danger",
          });
          toast.present();
        }
      } else {
        const toast = await toastController.create({
          message: i18n.t("noEmailAddress"),
          duration: 3000,
          color: "danger",
        });
        toast.present();
      }
    };

    // Define submit method
    const submit = async () => {
      if (showResetPassword.value === false) {
        await store.dispatch("login", {
          email: email.value,
          password: password.value,
        });
        router.push({ name: "Home" });
      }
    };

    return { email, password, submit, showResetPassword, resetPassword };
  },
});
