
// Imports
import {
  IonContent,
  IonHeader,
  IonPage,
  IonTitle,
  IonToolbar,
  IonRow,
  IonGrid,
  IonCol,
  IonButton,
  IonButtons,
  IonIcon,
} from "@ionic/vue";
import { defineComponent } from "vue";
import store from "@/store";
import { arrowBack } from "ionicons/icons";
import { useRouter } from "vue-router";

export default defineComponent({
  name: "Settings",
  components: {
    IonContent,
    IonHeader,
    IonPage,
    IonTitle,
    IonToolbar,
    IonRow,
    IonGrid,
    IonCol,
    IonButton,
    IonButtons,
    IonIcon
  },
  setup() {
    // Define router
    const router = useRouter();

    // logout function
    const logout = async () => {
      await store.dispatch("logout");
      router.replace({ name: "Login" });
    };

    return { logout, arrowBack };
  },
});
